.cardVideogameContainer{
  display: inline-grid;
  grid-gap: 0.7rem;
  margin: 0.005rem;
  padding: 0rem;
  border-radius: 0.4rem;
  overflow: hidden;
  border: solid 0.07rem rgba(255, 255, 255, 0);
  justify-items: center;
  width: min-content;
  height: 7rem; 
  transition: transform 500ms ease;
  cursor: pointer;
  /* width: 14rem;
  height: 7rem; */
}
.cardVideogameContainer:hover{
  border: solid 0.07rem rgba(98, 12, 14, 1);
  box-shadow: rgb(0 0 0 / 75%) 0px 3px 10px;
  transform:scale(1.1);
}
.imgVgContainer{
  width: 15rem;
  height: 10rem
}
.imgVg{
  width: 15rem;
  height: 10rem
}
.cardVideogameContainer:hover
.imgVgContainer{
  width: 15rem;
  height: 0.5rem;
}
.cardVideogameContainer:hover
.gamesInfoContainer{
  width: 100%;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 1),
    rgba(0, 0, 0, 0.65) 70%,
    rgba(0,0,0,0)
    );
  transition: color 600ms ease;
  color:rgba(255, 255, 255, 1)
}
.gameNameContainer{
  display: contents;
  font-size: 1rem;
  margin:0rem
}
.gameGenres{
  font-size: 0.8rem;
}
.gamesInfoContainer{
  width: 100%;
  color:rgba(255, 255, 255, 0)
}
