.Header{
  width: 100%;
  height: 6vh;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
}
.logoHeaderHomeConteiner{
  margin: 1vw;
  color: #ff00006d;
  font-weight: 300;
  font-size: 1.4rem;
  cursor: pointer;
}
.pageHeaderPageConteiner{
  padding-top: 0.7vh;
}
.inputHeaderHomeConteiner{
  margin: 1vw;
}
.inputHeaderHome {
  font-size: 0.74rem;
  width: 11rem;
  height: 1.1rem;
  border-width: 0px;
  color: rgb(118 118 118);
  background-color: rgb(59, 59, 59);
  border-radius: 0.2rem;
  border-width: 0.01rem;
  border-color: #780b0b;
  border-style: solid;
}
input[type="range"]{
  background: rgb(118 118 118);
  border: solid 1px #b40002;
  /* background: linear-gradient(to right, rgb(118 118 118) 0%, rgb(255 255 255) 100%);
  border: solid 1px #b40002;
  border-radius: 8px;
  outline: none;
  transition: background 450ms ease-in;
  -webkit-appearance: slider-horizontal; */
}
.homeContent {
  margin-top: 3rem;
  background: rgb(20, 20, 20);
  color: rgb(211 211 211);
  width:100%;
}
#Video {
  position: absolute;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  opacity: 1;
}